import { NInput } from 'naive-ui';
import { dialog, message } from './discrete';
import store from './store';

export const showTextDLg = ({ title = ``, content = '' }) => {
  dialog.success({
    title,
    style: {
      width: `800px`,
    },
    content() {
      return (
        <NInput
          type="textarea"
          autosize={{
            minRows: 8,
            maxRows: 16,
          }}
          inputProps={{
            style: `white-space: nowrap;`,
          }}
          value={content}
        />
      );
    },
    positiveText: ``,
    onPositiveClick() {
      navigator.clipboard
        .writeText(content)
        .then(() => {
          message.success(``);
        })
        .catch(() => {
          message.success(``);
        });
      return false;
    },
  });
};

